import { connect } from 'react-redux';
import { Dispatch } from 'redux';

import * as actions from '../actions';

import SetUsernameDialog from '../components/SetUsernameDialog';
import { StoreState } from '../types';
import { AppState } from 'src/constants';

export function mapStateToProps({ username }: StoreState) {
  return {
    username,
  }
}

export function mapDispatchToProps(dispatch: Dispatch<actions.MugginsActions>) {
  return {
    onUpdateUsername: (payload: string) =>  {
      dispatch(actions.updateUsername(payload));
      dispatch(actions.updateGameState(AppState.STATE_IN_LOBBY));
    },
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(SetUsernameDialog);



