import * as React from 'react';

import { WithStyles, Theme, withStyles, createStyles } from '@material-ui/core/styles';

import ListItemSecondaryAction from '@material-ui/core/ListItemSecondaryAction';
import ListItemText from '@material-ui/core/ListItemText';
import ListItem from '@material-ui/core/ListItem';
import IconButton from '@material-ui/core/IconButton';
import Paper from '@material-ui/core/Paper';
import Button from '@material-ui/core/Button';
import List from '@material-ui/core/List';
import Tooltip from '@material-ui/core/Tooltip';

import JoinIcon from '@material-ui/icons/ArrowForward'

import { ActiveGame } from 'src/types';

const styles = (theme: Theme) => createStyles({
  root: {
    margin: '0 auto',
    maxWidth: 800
  },
  contentContainer: {
    ...theme.mixins.gutters(),
  }
});

export interface Props extends WithStyles<typeof styles> {
  activeGames: ActiveGame[]
  onCreateGame: () => void
  onGetGameList: () => void
  onJoinGame: (gameId: string) => void
}

class GamesList extends React.Component<Props> {

  public componentWillMount() {
    this.props.onGetGameList();
  }

  public render() {

    const { activeGames, classes } = this.props;

    return (
      <Paper className={classes.root}>
        <div className={classes.contentContainer}>
          <Button onClick={this.props.onCreateGame}>Create Game</Button>
          <Button onClick={this.props.onGetGameList}>Refresh Games</Button>
          <List dense>
            {activeGames.map((game: ActiveGame) => (
              <ListItem key={game.id}>
                  <ListItemText
                    primary={"Players:" + game.players.join(', ')}
                  />
                  <ListItemSecondaryAction>
                    <Tooltip title={game.open ? "Join" : "Game Full"}>
                      <div>
                        <IconButton
                          aria-label="Join"
                          onClick={this.handleJoin(game.id)}
                          disabled={!game.open}
                        >
                          <JoinIcon />
                        </IconButton>
                      </div>
                    </Tooltip>
                  </ListItemSecondaryAction>
              </ListItem>
            ))}
          </List>
        </div>
      </Paper>
    )
  }

  private handleJoin = (gameId: string) => () => {
    console.log('Joining game: ' + gameId);
    this.props.onJoinGame(gameId);
  }

}

export default withStyles(styles)(GamesList);



