import { connect } from 'react-redux';
import { Dispatch } from 'redux';

import { MugginsActions, updateUsername, updateGameState } from 'src/actions';
import { StoreState } from 'src/types';
import { AppState } from 'src/constants';

import App from 'src/components/App';


// Main App needs to know the current gamestate and will load appropriate container
// based on this state
export function mapStateToProps({ appState: gameState }: StoreState) {
  return {
    gameState
  }
}

export function mapDispatchToProps(dispatch: Dispatch<MugginsActions>) {
  return {
    onUpdateUsername: (payload: string) => {
      dispatch(updateUsername(payload));
      dispatch(updateGameState(AppState.STATE_IN_LOBBY));
    }
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(App);



