import { connect } from 'react-redux';
import { Dispatch } from 'redux';

import * as actions from 'src/actions';

import GamesList from 'src/components/Lobby/GamesList';
import { StoreState } from 'src/types';

export function mapStateToProps({ activeGames }: StoreState) {
  return {
    activeGames,
  }
}

export function mapDispatchToProps(dispatch: Dispatch<actions.MugginsActions>) {
  return {
    onCreateGame: () => dispatch(actions.createGame()),
    onJoinGame: (gameId: string) => dispatch(actions.joinGame(gameId)),
    onGetGameList: () => dispatch(actions.getGameList())
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(GamesList);



