import { MugginsActions } from '../actions';
import { StoreState } from '../types';
import {
    UPDATE_USERNAME,
    UPDATE_USERLIST,
    SET_GAME,
    UPDATE_GAME_STATE,
    RECEIVE_CHAT_MESSAGE,
    UPDATE_GAME_LIST,
    RECEIVE_POINTS_MESSAGE
} from '../constants';

export function username(state: StoreState, action: MugginsActions): StoreState {
    switch(action.type) {
        case UPDATE_USERNAME: {
            sessionStorage.setItem("username", action.payload);
            return {...state, username: action.payload}
        }
        case UPDATE_GAME_STATE: {
            return {...state, appState: action.payload, pointsLog: []}
        }
        case UPDATE_USERLIST: {
            return {...state, onlineUsers: action.payload}
        }
        case UPDATE_GAME_LIST: {
            return {...state, activeGames: action.payload}
        }
        case SET_GAME: {
            return {...state, gameState: action.payload}
        }
        case RECEIVE_CHAT_MESSAGE: {
            let newChat = [...state.chatlog];

            if (newChat.length > 100) {
                newChat = newChat.slice(1);
            }
            newChat.push(action.payload);

            return {...state, chatlog: newChat}
        }
        case RECEIVE_POINTS_MESSAGE: {
            let newPoints = [...state.pointsLog];

            if (newPoints.length > 100) {
                newPoints = newPoints.slice(1);
            }
            newPoints.push(action.payload);

            return {...state, pointsLog: newPoints}
        }
        default: {
            return state;
        }
    }
}



