import * as SignalR from '@aspnet/signalr';
import * as Constants from '../constants';
import { MugginsActions, receiveChatMessage, setGame, updateGameState, updateUserList, updateGameList, receivePointsMessage } from '../actions';

import { Store } from 'redux';
import { StoreState, ActiveGame, IGame } from 'src/types';

const connection = new SignalR.HubConnectionBuilder()
    .withUrl(Constants.SIGNAL_R_URL)
    .configureLogging(SignalR.LogLevel.Information)
    .build();

export function signalRInvokeMiddleware(store: any) {
    return (next: any) => async (action: MugginsActions) => {
        switch (action.type) {
        case Constants.UPDATE_USERNAME: 
            connection.invoke('UpdateUsername', action.payload);
            break;
        case Constants.JOIN_GAME: 
            connection.invoke('JoinGame', action.payload);
            break;
        case Constants.CREATE_GAME: 
            connection.invoke('CreateGame');
            break;
        case Constants.START_GAME: 
            connection.invoke('StartGame');
            break;
        case Constants.CUT_DECK: 
            connection.invoke('CutDeck', action.payload);
            break;
        case Constants.SEND_TO_CRIB: 
            connection.invoke('SendToCrib', action.payload);
            break;
        case Constants.START_NEXT_HAND: 
            connection.invoke('StartNextHand');
            break;
        case Constants.PLAY_CARD:
            connection.invoke('PlayCard', action.payload);
            break;
        case Constants.GO:
            connection.invoke('Go');
            break;
        case Constants.SEND_CHAT_MESSAGE: 
            connection.invoke('SendChatMessage', action.payload);
            break;
        case Constants.GET_GAME_LIST:
            connection.invoke('GetGameList');
            break;
        }
        return next(action);
    }
}

export function signalRRegisterCommands(store: Store<StoreState, MugginsActions>, callback: () => void) {

    connection.on('UpdatePlayerList', (data: string[]) => {
        store.dispatch(updateUserList(data))
    });

    connection.on('UpdateGameState', (data: IGame) => {
        console.log(data);
        store.dispatch(setGame(data))
        if (store.getState().appState !== Constants.AppState.STATE_IN_GAME) {
            store.dispatch(updateGameState(Constants.AppState.STATE_IN_GAME));
        }
    });

    connection.on('ExitGame', () => {
        store.dispatch(updateGameState(Constants.AppState.STATE_IN_LOBBY))
    });

    connection.on('ReceiveChatMessage', (user: string, message: string) => {
        store.dispatch(receiveChatMessage({user, message}))
    })

    connection.on('ReceivePointsMessage', (message: string) => {
        store.dispatch(receivePointsMessage(message))
    })

    connection.on('UpdateGameList', (games: ActiveGame[]) => {
        console.log('Updating Games list', games)
        store.dispatch(updateGameList(games))
    })

    connection.start().then(callback);
}



