import { AppState } from 'src/constants';

export interface StoreState {
    username: string;
    onlineUsers: string[];
    activeGames: ActiveGame[];
    appState: AppState;
    chatlog: ChatMessage[];
    gameState: IGame;
    pointsLog: string[];
}

export interface ChatMessage {
    user: string;
    message: string;
}

export interface ActiveGame {
    players: string[];
    open: boolean;
    id: string
}

export interface IGame {
    players: string[];
    player1Ready: boolean;
    player2Ready: boolean;
    state: GameState;
    turn: number;
    hand: Card[];
    id: string;
    player: number;
    player1Cut: Card;
    player2Cut: Card;
    cardsPlayed: Card[];
    dealer: number;
    otherPlayersHand: Card[];
    crib: Card[];
    points: number[];
    cut: Card;
}

export enum GameState {
    WaitForStart = 1,
    CutForDealer,
    SelectCardsForCrib,
    CutForCount,
    Peg,
    Count,
    GameFinished,
}

export interface Card {
    suit: number,
    value: number,
    displayName: string
}



