import * as React from 'react';

import { WithStyles, Theme, withStyles, createStyles } from '@material-ui/core/styles';

import Typography from '@material-ui/core/Typography';
import Paper from '@material-ui/core/Paper';
import Toolbar from '@material-ui/core/Toolbar';

import Chat from 'src/containers/Lobby/Chat';
import GamesList from 'src/containers/Lobby/GamesList';

const styles = (theme: Theme) => createStyles({
  root: {
    margin: '0 auto',
    maxWidth: 800
  },
  spacer: {
    flexGrow: 1
  },
  textField: {
    marginLeft: theme.spacing.unit,
    marginRight: theme.spacing.unit,
    width: 200,
  },
  contentContainer: {
    ...theme.mixins.gutters(),
  }
});

export interface Props extends WithStyles<typeof styles> {
  username: string
  onlineUsers: string[]
  onUpdateUsername: (payload: string) => void
}

interface State {
  username: string
}

class Lobby extends React.Component<Props, State> {

  constructor(props: Props) {
    super(props);
    this.state = { username: props.username }
  }

  public render() {

    const { onlineUsers, classes } = this.props;

    let prompt = "";

    if (onlineUsers.length === 1) {
      prompt = "There is currently 1 player online.";
    } else {
      prompt = "There are currently " + onlineUsers.length + " players online.";
    }

    return (
      <div className={classes.root}>
      <Paper>
        <Toolbar>
          <Typography variant="h5" component="h3">
            Hello {this.props.username}!
          </Typography>
        </Toolbar>
      </Paper>
      <GamesList />
      <Chat />
      <Paper>
        <Typography component="p" align="right">
          {prompt}
        </Typography>
      </Paper>
      </div>
    )
  }

}

export default withStyles(styles)(Lobby);



