import * as constants from '../constants';
import { ChatMessage, ActiveGame, IGame } from 'src/types';

export interface UpdateUsername {
    type: constants.UPDATE_USERNAME;
    payload: string;
}

export function updateUsername(payload: string): UpdateUsername {
    return {
        type: constants.UPDATE_USERNAME,
        payload
    }
}

export interface UpdateGameState {
    type: constants.UPDATE_GAME_STATE;
    payload: constants.AppState;
}

export function updateGameState(payload: constants.AppState): UpdateGameState {
    return {
        type: constants.UPDATE_GAME_STATE,
        payload
    }
}

export interface UpdateUserList {
    type: constants.UPDATE_USERLIST;
    payload: string[];
}

export function updateUserList(payload: string[]): UpdateUserList {
    return {
        type: constants.UPDATE_USERLIST,
        payload
    }
}

/** Any action to do with listing/creating/joining games */

// Join Game
export interface JoinGame {
    type: constants.JOIN_GAME;
    payload: string;
}

export function joinGame(gameId: string) : JoinGame {
    return {
        type: constants.JOIN_GAME,
        payload: gameId
    }
};

// Set Game (This will be called from the server if we have succesfully created or joined a game)
export interface SetGame {
    type: constants.SET_GAME;
    payload: IGame;
}

export function setGame(game: IGame) : SetGame {
    return {
        type: constants.SET_GAME,
        payload: game
    }
}

export interface CreateGame {
    type: constants.CREATE_GAME;
}

export function createGame(): CreateGame {
    return {
        type: constants.CREATE_GAME
    }
}

export interface UpdateGameList {
    type: constants.UPDATE_GAME_LIST;
    payload: ActiveGame[];
}

export function updateGameList(games: ActiveGame[]) : UpdateGameList {
    return {
        type: constants.UPDATE_GAME_LIST,
        payload: games
    }
}

/**
 * Send a request for an updated list of games to the server
 */
export interface GetGameList {
    type: constants.GET_GAME_LIST;
}

export function getGameList() : GetGameList {
    return {
        type: constants.GET_GAME_LIST
    }
}

type GameListActions = JoinGame | CreateGame | SetGame | UpdateGameList | GetGameList;


/*********************************
 * Chat
 *********************************/

 // Send Message
export interface SendChatMessage {
    type: constants.SEND_CHAT_MESSAGE;
    payload: string;
}

export function sendChatMessage(payload: string): SendChatMessage {
    return {
        type: constants.SEND_CHAT_MESSAGE,
        payload
    }
}

// Receive Message
export interface ReceiveChatMessage {
    type: constants.RECEIVE_CHAT_MESSAGE;
    payload: ChatMessage;
}

export function receiveChatMessage(payload: ChatMessage): ReceiveChatMessage {
    return {
        type: constants.RECEIVE_CHAT_MESSAGE,
        payload
    }
}

type ChatActions = SendChatMessage | ReceiveChatMessage;



/*********************************
 * In Game Actions
 *********************************/

// PLayer is ready/start game
export interface StartGame {
    type: constants.START_GAME;
}

export function startGame(): StartGame {
    return {
        type: constants.START_GAME,
    }
}

// PLayer is cutting the deck at a specified position
export interface CutDeck {
    type: constants.CUT_DECK;
    payload: number;
}

export function cutDeck(payload: number): CutDeck {
    return {
        type: constants.CUT_DECK,
        payload
    };
}

// PLayer is sending the two selected card indexes into the crib.
export interface SendToCrib {
    type: constants.SEND_TO_CRIB;
    payload: number[];
}

export function sendToCrib(payload: number[]): SendToCrib {
    return {
        type: constants.SEND_TO_CRIB,
        payload
    };
}
    
    
// PLayer is playing the selected card index for pegging
export interface PlayCard {
    type: constants.PLAY_CARD;
    payload: number;
}

export function playCard(payload: number): PlayCard {
    return {
        type: constants.PLAY_CARD,
        payload
    };
}

// PLayer can't put anything down and must Go
export interface Go {
    type: constants.GO;
}

export function go(): Go {
    return {
        type: constants.GO,
    };
}

// Server is sending an update on how points were scored
export interface ReceivePointsMessage {
    type: constants.RECEIVE_POINTS_MESSAGE;
    payload: string
}

export function receivePointsMessage(message: string): ReceivePointsMessage {
    return {
        type: constants.RECEIVE_POINTS_MESSAGE,
        payload: message
    };
}

// Signal that we're ready to start the next hand
export interface StartNextHand {
    type: constants.START_NEXT_HAND;
}

export function startNextHand(): StartNextHand {
    return {
        type: constants.START_NEXT_HAND,
    };
}

type InGameActions = StartGame | CutDeck | SendToCrib | PlayCard | Go | ReceivePointsMessage | StartNextHand;

/**
 * All Action types combined into one
 */

export type MugginsActions = UpdateUsername | UpdateUserList | GameListActions | UpdateGameState | ChatActions | InGameActions;










