import { connect } from 'react-redux';
import { Dispatch } from 'redux';

import * as actions from 'src/actions';

import Chat from 'src/components/Lobby/Chat';
import { StoreState } from 'src/types';

export function mapStateToProps({ chatlog }: StoreState) {
  return {
    chatlog,
  }
}

export function mapDispatchToProps(dispatch: Dispatch<actions.MugginsActions>) {
  return {
    onSendMessage: (payload: string) => dispatch(actions.sendChatMessage(payload)),
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(Chat);



