import * as React from 'react';

import { Theme, createStyles, withStyles, WithStyles } from '@material-ui/core/styles';

import Lobby from '../containers/Lobby/Lobby';
import Game from '../containers/Game';
import SetUsernameDialog from '../containers/SetUsernameDialog';

import { AppState } from 'src/constants';

export interface Props extends WithStyles<typeof styles> {
  gameState: AppState
  onUpdateUsername: (username: string) => void
}

const styles = (theme: Theme) => createStyles({
  container: {
    paddingTop: theme.spacing.unit * 2,
    paddimgBottom: theme.spacing.unit * 2,
  }
});

class App extends React.Component<Props> {

  // We are going to use session storage to remember that persons username
  // Mostly for debug so that i can refresh tabs and not lose my username
  public componentWillMount() {
    const username: string = sessionStorage.getItem("username") || "";
    console.log(username);
    if (username !== "") {
      this.props.onUpdateUsername(username);
    }
  }

  public render() {
    const { classes } = this.props;
    return (
      <div className={classes.container}>
        { this.props.gameState === AppState.STATE_SET_USERNAME && <SetUsernameDialog /> }
        { this.props.gameState === AppState.STATE_IN_LOBBY && <Lobby /> }
        { this.props.gameState === AppState.STATE_IN_GAME && <Game /> }
      </div>
    );
  }
}

export default withStyles(styles)(App);



