import * as React from 'react';

import { WithStyles, Theme, withStyles, createStyles } from '@material-ui/core/styles';

import Typography from '@material-ui/core/Typography';
import Paper from '@material-ui/core/Paper';
import TextField from '@material-ui/core/TextField';
import InputAdornment from '@material-ui/core/InputAdornment';
import IconButton from '@material-ui/core/IconButton';
import Divider from '@material-ui/core/Divider';
import Toolbar from '@material-ui/core/Toolbar';

import Send from '@material-ui/icons/Send';

import {ChatMessage} from 'src/types';

const styles = (theme: Theme) => createStyles({
  root: {
    margin: '0 auto',
    maxWidth: 800
  },
  spacer: {
    flexGrow: 1
  },
  textField: {
    marginLeft: theme.spacing.unit,
    marginRight: theme.spacing.unit,
    width: 200,
  },
  contentContainer: {
    ...theme.mixins.gutters(),
  }
});

export interface Props extends WithStyles<typeof styles> {
  chatlog: ChatMessage[],
  onSendMessage: (payload: string) => void
}

interface State {
  message: string
}

class Chat extends React.Component<Props, State> {

  constructor(props: Props) {
    super(props);
    this.state = { message: "" }
  }

  public render() {

    const { classes } = this.props;

    return (
      <div className={classes.root}>
        <Paper>
          <Toolbar>
            <Typography variant="h5">
              Chat
            </Typography>
          </Toolbar>
          <Divider />
          <TextField
            disabled
            multiline
            fullWidth
            rows={20}
            value={this.props.chatlog.map(m => m.user + ': ' + m.message).join('\n')}
          />
          <Divider />
          <TextField
            fullWidth
            value={this.state.message}
            onChange={this.handleChange("message")}
            onKeyPress={this.handleKeyPress("message")}
            placeholder={"Send a Message..."}
            InputProps={{
              endAdornment:
                <InputAdornment position="end">
                  <IconButton
                    aria-label="Toggle password visibility"
                    onClick={this.handleSendMessage}
                    color="primary"
                    disabled={this.state.message === ""}
                  >
                    <Send />
                  </IconButton>
                </InputAdornment>,
            }}
          />
        </Paper>
      </div>
    )
  }

  private handleChange = (name: string) => (event: React.ChangeEvent<HTMLTextAreaElement>) => {
    const newState = {...this.state};
    newState[name] = event.target.value;
    this.setState(newState);
  }

  private handleKeyPress = (name: string) => (event: React.KeyboardEvent<HTMLInputElement>) => {
    if (event.key === "Enter") {
      this.handleSendMessage();
    }
  }

  private handleSendMessage = () => {
    if (this.state.message !== "") {
      this.props.onSendMessage(this.state.message);
      this.setState({...this.state, message: ""});
    }
  }
}

export default withStyles(styles)(Chat);



