import { connect } from 'react-redux';
import { Dispatch } from 'redux';

import Game from '../components/Game';
import { StoreState } from '../types';
import { MugginsActions, startGame, cutDeck, sendToCrib, playCard, go, startNextHand } from 'src/actions';

function mapStateToProps({ gameState, pointsLog }: StoreState) {
  return {
    game: gameState,
    pointsLog,
  }
}

function mapDispatchToProps(dispatch: Dispatch<MugginsActions>) {
  return {
    onPlayerReady: () => dispatch(startGame()),
    onCut: (position: number) => dispatch(cutDeck(position)),
    onSendToCrib: (cardIndexes: number[]) => dispatch(sendToCrib(cardIndexes)),
    onPlayCard: (cardIndex: number) => dispatch(playCard(cardIndex)),
    onStartNextHand: () => dispatch(startNextHand()),
    onGo: () => dispatch(go()),
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(Game);



