import * as React from 'react';

import { WithStyles, Theme, withStyles, createStyles } from '@material-ui/core/styles';

import Typography from '@material-ui/core/Typography';
import Paper from '@material-ui/core/Paper';
import Toolbar from '@material-ui/core/Toolbar';
import { IGame, GameState } from 'src/types';
import { Button } from '@material-ui/core';

import Card from './Card';

const styles = (theme: Theme) => createStyles({
  root: {
    margin: '0 auto',
    maxWidth: 800
  },
  hand: {
    display: 'flex',
    margin: theme.spacing.unit * 2,
    justifyContent: 'center'
  },
  controls: {
    display: 'flex',
    margin: theme.spacing.unit * 2
  },
  spacer: {
    flexGrow: 1
  },
  score: {
    display: 'flex',
    justifyContent: 'center',
  },
  scoreText: {
    margin: '0 auto'
  }
});

export interface Props extends WithStyles<typeof styles> {
  game: IGame,
  pointsLog: string[],
  onPlayerReady: () => void,
  onCut: (position: number) => void,
  onSendToCrib: (cardIndexes: number[]) => void,
  onPlayCard: (cardIndex: number) => void,
  onStartNextHand: () => void,
  onGo: () => void,
}

interface State {
  selectedCards: number[],
}

class Game extends React.Component<Props, State> {

  constructor(props: Props) {
    super(props);

    this.state = {
      selectedCards: []
    }
  }

  public render() {

    const { classes, game } = this.props;
    const isReady = (game.player === 1 && game.player1Ready) || (game.player === 2 && game.player2Ready);
    const playerMadeCutForDeal = ( game.player === 1 && game.player1Cut !== null ) || ( game.player === 2 && game.player2Cut !== null );
    const bothPlayersReady = (game.player1Ready && game.player2Ready);

    return (
      <div className={classes.root}>
        <Paper>
          <Toolbar>
            <Typography variant="h5" component="h3">
              Game {this.props.game.id}
            </Typography>
          </Toolbar>
          Players: {this.props.game.players.join(', ')}
          <Button disabled={isReady || game.state !== GameState.WaitForStart} onClick={this.props.onPlayerReady}>Ready!</Button>
          <Button disabled={!bothPlayersReady || playerMadeCutForDeal} onClick={this.handleCut(10)}>Cut For Deal</Button>
        </Paper>
        <Paper className={classes.score}>
          <Typography variant="h5" component="h3" className={classes.scoreText}>
              {(this.props.game.players ? this.props.game.players[0] : 'No Player' ) + ': ' +(this.props.game.points ? this.props.game.points[0] : 0)}
          </Typography>
          <Typography variant="h5" component="h3" className={classes.scoreText}>
              {(this.props.game.players ? this.props.game.players[1] : 'No Player' ) + ': ' + (this.props.game.points ? this.props.game.points[1] : 0)}
          </Typography>
        </Paper>
        <Paper className={classes.score}>
          <Typography variant="h5" component="h3" className={classes.scoreText}>
              {this.props.game.dealer ? (this.props.game.dealer  === this.props.game.player ? "Your Crib!" : "Their Crib...") : "Dealer Undecided..."}
          </Typography>
        </Paper>
        <Paper>
          {this.renderGameBoard()}
        </Paper>
        {game.state === GameState.Peg &&
          <Paper>
            <Toolbar>
            <Typography variant="h5" component="h3">
              Cards Played
            </Typography>
          </Toolbar>
            {this.renderCardsPlayed()}
          </Paper>
        }
        <Paper>
          {this.props.pointsLog && this.props.pointsLog.map((m,i) => <p key={i}>{m}</p>)}
        </Paper>
      </div>
    )
  }

  private renderCardsPlayed() : JSX.Element {
    const { game, classes } = this.props;
    const currentCount = game.cardsPlayed.reduce((sum : number, curr) => sum + (curr.value > 10 ? 10 : curr.value), 0);

    return (
      <div>
        <div className={classes.hand}>
          {game.cardsPlayed.map((c,index) => (
            <Card key={c.displayName} suit={c.suit} value={c.value} onClick={this.handleCardClicked(index)} />
          ))}
        </div>
        <div className={classes.hand}>
          <Typography variant="h5" component="h3">
            Count: {currentCount}
          </Typography>
        </div>
        {currentCount > 21 &&
          <div className={classes.hand}>
            <Typography variant="body2" component="h3">
              You must play a {31 - currentCount} or Less
            </Typography>
          </div>
          }
      </div>
    );
  }

  private renderGameBoard() : JSX.Element {
    const { game, classes } = this.props;


    if (game.state === GameState.CutForDealer) {
      return (<p>Looks like we're waiting to cut for deal!</p>)
    }

    if (game.state === GameState.GameFinished) {
      if (game.points[game.player-1] >= 121) {
        return (<p>You Won!</p>)
      }
      else {
        return (<p>You Lost!</p>)
      }
    }

    if (game.state === GameState.SelectCardsForCrib || game.state === GameState.Peg || game.state === GameState.Count || game.state === GameState.CutForCount) {
      return (
        <div>
          {game.state === GameState.Count &&
          <div>
            <div className={classes.hand}>
              <Typography variant="h5" component="h3">
                Their Hand
              </Typography>
            </div>
            <div className={classes.hand}>
              {game.otherPlayersHand.map((c,index) => (
                <Card key={c.displayName} selected={this.state.selectedCards.includes(index)} suit={c.suit} value={c.value} onClick={this.handleCardClicked(index)} />
              ))}
            </div>
            <div className={classes.hand}>
              <Typography variant="h5" component="h3">
                The Crib
              </Typography>
            </div>
            <div className={classes.hand}>
              {game.crib.map((c,index) => (
                <Card key={c.displayName} selected={this.state.selectedCards.includes(index)} suit={c.suit} value={c.value} onClick={this.handleCardClicked(index)} />
              ))}
            </div>
            <div className={classes.hand}>
              <Typography variant="h5" component="h3">
                Your Hand
              </Typography>
            </div>
          </div>
          }
          <div className={classes.hand}>
            {game.hand.map((c,index) => (
              <Card key={c.displayName} selected={this.state.selectedCards.includes(index)} suit={c.suit} value={c.value} onClick={this.handleCardClicked(index)} />
            ))}
          </div>
          {(game.state === GameState.Peg || game.state === GameState.Count) && 
              <div className={classes.hand}>
                <Card suit={game.cut.suit} value={game.cut.value} />
              </div>
            }
          <div className={classes.controls}>
            <div className={classes.spacer}/>
            {game.state === GameState.SelectCardsForCrib && 
              <Button
                variant="contained"
                color="primary"
                disabled={this.state.selectedCards.length !== this.getAllowedNumberOfSelectedCards() || game.hand.length !== 6}
                onClick={this.handleSendToCrib}
              >
                Send to Crib
              </Button>
            }
            {game.state === GameState.CutForCount && 
              <Button
                variant="contained"
                color="primary"
                onClick={this.handleCut(11)}
                disabled={game.dealer === game.player}
              >
                {game.dealer === game.player ? "Waiting for other player to Cut..." : "Cut The Deck!"}
              </Button>
            }
            {game.state === GameState.Peg && 
              <Button
                variant="contained"
                color="primary"
                disabled={(this.state.selectedCards.length !== this.getAllowedNumberOfSelectedCards() && this.playerCanPlay()) || game.turn !== game.player}
                onClick={this.handlePlayCard}
              >
                {this.playerCanPlay() ? "Play Card" : "Go"}
              </Button>
            }
            {game.state === GameState.Count && 
              <Button
                variant="contained"
                color="primary"
                onClick={this.props.onStartNextHand}
                disabled={(game.player === 1 && game.player1Ready) || (game.player === 2 && game.player2Ready)}
              >
                Next Hand!
              </Button>
            }
          </div>
        </div>
      )
    }

    return (<p>"Nothing"</p>);
  }

  private getAllowedNumberOfSelectedCards = () : number => {
    switch(this.props.game.state) {
      case GameState.SelectCardsForCrib:
        return this.props.game.hand.length === 6 ? 2 : 0;
      case GameState.CutForDealer:
      case GameState.Peg:
        return this.playerCanPlay() ? 1 : 0;
      default:
        return 0;
    }
  }

  private playerCanPlay = () : boolean => {
    const currentCount = this.props.game.cardsPlayed.reduce((sum : number, curr) => sum + (curr.value > 10 ? 10 : curr.value), 0);
    
    console.log(currentCount);
    let canPlay = false;

    for (const c of this.props.game.hand) {
      if (c.value + currentCount <= 31) {
        canPlay = true;
      }
    }
    return canPlay;
  }

  private handleCut = (position: number) => () => {
    this.props.onCut(position);
  }

  private handleCardClicked = (index: number) => () => {
    let selectedCards = [...this.state.selectedCards];
    const currentIndex = selectedCards.indexOf(index);

    if (currentIndex >= 0) {
      // Card already selected, unselect it.
      selectedCards.splice(currentIndex, 1);
    } else if (this.getAllowedNumberOfSelectedCards() === 1) {
      // Only select a new card if we haven't already selected 2 for the crib
      const currentCount = this.props.game.cardsPlayed.reduce((sum : number, curr) => sum + (curr.value > 10 ? 10 : curr.value), 0);
      const selectedCardValue = this.props.game.hand[index].value > 10 ? 10 : this.props.game.hand[index].value;
      if (selectedCardValue + currentCount <= 31) {
        selectedCards = [index];
      }
    }
    else if(selectedCards.length < this.getAllowedNumberOfSelectedCards()){
        selectedCards.push(index);
    }

    this.setState({selectedCards});
  }

  private handleSendToCrib = () => {
    // Make sure we have the right amount selected.
    if (this.state.selectedCards.length === this.getAllowedNumberOfSelectedCards()) {
      // Send a signal to the server containing the array indexes of the cards to send to the crib
      this.props.onSendToCrib([...this.state.selectedCards]);
    }
    this.setState({...this.state, selectedCards: []})
  }

  private handlePlayCard = () => {
    if (this.state.selectedCards.length === 1) {
      console.log('playingCard');
      this.props.onPlayCard(this.state.selectedCards[0]);
    } else if (!this.playerCanPlay()) {
      this.props.onGo();
    }
    this.setState({...this.state, selectedCards: []});
  }

}

export default withStyles(styles)(Game);



