import { connect } from 'react-redux';
import { Dispatch } from 'redux';

import * as actions from 'src/actions';

import Lobby from 'src/components/Lobby/Lobby';
import { StoreState } from 'src/types';

export function mapStateToProps({ username, onlineUsers }: StoreState) {
  return {
    username,
    onlineUsers,
  }
}

export function mapDispatchToProps(dispatch: Dispatch<actions.MugginsActions>) {
  return {
    onUpdateUsername: (payload: string) => dispatch(actions.updateUsername(payload)),
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(Lobby);



