import * as React from 'react';

import Dialog from '@material-ui/core/Dialog';
import DialogTitle from '@material-ui/core/DialogTitle';
import TextField from '@material-ui/core/TextField';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogActions from '@material-ui/core/DialogActions';
import Button from '@material-ui/core/Button';

export interface Props {
  username?: string
  onUpdateUsername: (payload: string) => void
}

interface State {
  username: string
}

class SetUsernameDialog extends React.Component<Props, State> {

  constructor(props: Props) {
    super(props);
    this.state = {
      username: props.username ? props.username : ""
    }
  }

  public render() {

    return (
      <Dialog
        open
        aria-labelledby="form-dialog-title"
      >
        <DialogTitle id="form-dialog-title">Choose Username</DialogTitle>
        <DialogContent>
          <DialogContentText>
            To continue you must decide on a username.
          </DialogContentText>
          <TextField
            autoFocus
            margin="dense"
            id="name"
            label="Username"
            fullWidth
            value={this.state.username}
            onChange={this.handleChange}
            onKeyPress={this.handleKeyPress}
            autoComplete="off"
          />
        </DialogContent>
        <DialogActions>
          <Button onClick={this.handleUpdateUsername} color="primary" disabled={this.state.username === ""}>
            Play!
          </Button>
        </DialogActions>
      </Dialog>
    )
  }

  /**
   * Handler for updating the username
   */
  private handleUpdateUsername = (event: React.MouseEvent<HTMLElement, MouseEvent>) => {
    this.props.onUpdateUsername(this.state.username);
  }

  /**
   * General state update for any html input element
   */
  private handleChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    const nextState = {
      username: event.target.value
    };

    this.setState(nextState);
  }

  /**
   * We want to submit the new username if the user presses enter in the text field
   */
  private handleKeyPress = (event: React.KeyboardEvent<HTMLDivElement>) => {
    if(event.key === 'Enter') {
      if(this.state.username !== "") {
        this.props.onUpdateUsername(this.state.username);
      }
      event.preventDefault();
    }
  }
}

export default SetUsernameDialog;



