import * as React from 'react';
import * as ReactDOM from 'react-dom';
import { Provider } from 'react-redux';
import { createStore, Action, applyMiddleware } from 'redux';

import { MuiThemeProvider, createMuiTheme } from '@material-ui/core/styles';
import purple from '@material-ui/core/colors/purple';
import CssBaseline from '@material-ui/core/CssBaseline';

import App from './containers/App';
import registerServiceWorker from './registerServiceWorker';
import { username } from './reducers';
import { StoreState } from './types';
import * as SignalRMiddleware from './middleware/signalr';
import { AppState } from './constants';


const store = createStore<StoreState, Action<any>, any, any>(username, {
    username: '',
    onlineUsers: [],
    chatlog: [],
    appState: AppState.STATE_SET_USERNAME,
    activeGames: [],
    pointsLog: [],
    gameState: {
      players: [],
      points: [0,0],
      state: undefined,
      turn: undefined,
      hand: undefined,
      id: undefined,
    }
  }, applyMiddleware(SignalRMiddleware.signalRInvokeMiddleware)
);

// Create our material-ui theme to make it look pretty and stuff
// TODO: play with colors and find a good theme
const theme = createMuiTheme({
  palette: {
    primary: { main: purple[500] }, // Purple and green play nicely together.
    secondary: { main: '#11cb5f' }, // This is just green.A700 as hex.
  },
  typography: { useNextVariants: true },
});

function Root() {
  return (
    <Provider store={store}>
      <MuiThemeProvider theme={theme}>
        <CssBaseline />
        <App />
      </MuiThemeProvider>
    </Provider>
  );
}
SignalRMiddleware.signalRRegisterCommands(store, () => {
  ReactDOM.render(
    <Root />,
    document.getElementById('root') as HTMLElement
  );
});

registerServiceWorker();



